.oo-arrow::after {
    -webkit-transform: rotate(135deg);
            transform: rotate(135deg);
}
.oo-ck {
  position: absolute;
  clip: rect(0, 0, 0, 0);
}
.oo-ck:checked + .oo-menu-group .oo-arrow::before {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
